import http from '@/components/utils/http.js'
export default {

	data: function() {
		return {
		 
			pageLoad: false,
			pageData: {
				catList:{
					catid:1,
					title:'美食',
					pid:0,
					orderindex:0,
					status:1,
					imgurl:'',
					description:''
				}
			},
			catid:0,
			height:440,
			sideTop:44
		}
	},
	onLoad: function(ops) {
		var win=uni.getSystemInfoSync();
		this.height=win.windowHeight-50;

		this.sideTop=0;

		// this.getPage();
		
	},
	methods: {
		setCat:function(catid){
			this.catid=catid;
		},
		goList:function(catid){
			uni.navigateTo({
				url:"../b2c_product/list?catid="+catid
			})
		},
		getPage: function() {
			var that = this;
			that.app.get({
				url: that.app.apiHost + "/module.php?m=b2c_category&ajax=1",
				success: function(res) {
					that.pageData = res.data;
					for(var i in res.data.catList){
						that.catid=res.data.catList[i].catid;
						break;
					}
					
					that.pageLoad = true;
				}
			})
		}
	}
}
